import React, { useState, useEffect } from "react";
import CryptoJS from "crypto-js";
import './PayPage.css';
import { useNavigate } from 'react-router-dom';

const PayPage = ({ buttonNavigation }) => {
    const navigate = useNavigate();
    const [isAnimating, setIsAnimating] = useState(false); // Состояние для анимации

    useEffect(() => {
        if (buttonNavigation) {
            setIsAnimating(false); // Убираем класс через секунду
        }
    }, [buttonNavigation]); // Эффект срабатывает, если переход был через кнопку

    const handleGoBack = () => {
        setIsAnimating(true); // Запускаем анимацию при уходе
        setTimeout(() => {
          navigate('/'); // После завершения анимации возвращаемся на главную
        }, 1000); // Устанавливаем длительность анимации
      };    

    // Ваш секретный ключ
    const secretKey = "b73867256fae2caf0822f58ef2f36d10dad0c433";

    // Данные заказа
    const baseOrderData = {
        merchantAccount: "freelance_user_67474e9170d99",
        merchantDomainName: "www.market.ua",
        amount: "100.00",
        currency: "USD",
        productName: ["Product Name"],
        productPrice: ["1000"],
        productCount: ["1"],
        language: "EN",
    };

    // Генерация уникального orderReference
    const generateOrderReference = () => {
        return `ORDER-${Date.now()}`; // Уникальный идентификатор на основе временной метки
    };

    // Генерация merchantSignature
    const generateMerchantSignature = (data) => {
        const signatureData = [
            data.merchantAccount,
            data.merchantDomainName,
            data.orderReference,
            data.orderDate,
            data.amount,
            data.currency,
            ...data.productName,
            ...data.productCount,
            ...data.productPrice,
        ].join(";");

        return CryptoJS.HmacMD5(signatureData, secretKey).toString();
    };

    // Инициализация оплаты
    const initializePayment = () => {
        // Уникальные параметры заказа
        const orderReference = generateOrderReference();
        const orderDate = Math.floor(Date.now() / 1000).toString(); // Временная метка в секундах

        const orderData = {
            ...baseOrderData,
            orderReference,
            orderDate,
        };

        // Генерация подписи
        const merchantSignature = generateMerchantSignature(orderData);

        // Инициализация WayForPay
        const wayforpay = new window.Wayforpay();
        wayforpay.run(
            {
                ...orderData,
                merchantSignature, // Добавляем сгенерированный хэш
            },
            function (response) {
                console.log("Payment approved:", response); // Успешный платёж
            },
            function (response) {
                console.log("Payment declined:", response); // Отказ в платеже
            },
            function (response) {
                console.log("Payment pending or processing:", response); // Ожидание или обработка
            }
        );
    };

    // Загрузка скрипта WayForPay
    const loadWayforpayScript = () => {
        if (!document.getElementById("widget-wfp-script")) {
            const script = document.createElement("script");
            script.id = "widget-wfp-script";
            script.src = "https://secure.wayforpay.com/server/pay-widget.js";
            script.type = "text/javascript";
            script.async = true;
            document.body.appendChild(script);
        }
    };

    // Загрузка скрипта при монтировании компонента
    useEffect(() => {
        loadWayforpayScript();
    }, []);

    return (
        <div className="pop-up-page">
            <div className="popup-overlay">
                <div
                    className="popup-container"
                    onClick={(e) => e.stopPropagation()} // Останавливаем всплытие
                >
                    <div className="popup-content">
                        <h2 data-aos="fade">Want to Buy Now?</h2>
                        <p data-aos="fade" data-aos-delay="200">Choose a convenient payment method for you:</p>
                        <div className="pop-up-buttons">
                            <div className="pop-up-btn" data-aos="fade" data-aos-delay="600">
                                <h4>Bank</h4>
                                <button
                                    id="wayforpay"
                                    onClick={initializePayment}
                                >
                                    <img src="/levitation_gh/images/pop-up/logo_1.svg" alt="Bank" />
                                </button>
                            </div>
                            <div className="pop-up-btn" data-aos="fade" data-aos-delay="1000">
                                <h4>Crypto</h4>
                                <button>
                                    <img src="/levitation_gh/images/pop-up/logo_2.svg" alt="Crypto" />
                                </button>
                            </div>
                        </div>
                        <a
                            className="navbar-button"
                            data-aos="fade" 
                            data-aos-delay="1200"
                            onClick={handleGoBack} // Обработчик клика
                        >
                            Go Back
                        </a>
                    </div>
                </div>

            </div>
            {/* <div className={`animation-transition ${isAnimating ? 'animating' : ''}`}></div> */}
        </div>
    )
}

export default PayPage;
