import React, { useState } from "react";
import "./Accordion.css";
import "./AccordionMediaRequires.css";

const AccordionItem = ({ title, content, isOpen, onClick }) => {
    return (
        <div className="accordion-item">
            <button className="accordion-title" onClick={onClick}>
                <span>{title}</span>
                <div className={`accordion-button ${isOpen ? "active" : ""}`}>
                    <div className="obj obj1">
                        <img src="/levitation_gh/images/faq-section/icon.svg" alt="" />
                    </div>
                    <div className="obj obj2">
                        <img src="/levitation_gh/images/faq-section/icon.svg" alt="" />
                    </div>
                </div>
            </button>
            <div
                className="accordion-content-wrapper"
                style={{
                    maxHeight: isOpen ? "200rem" : "0",
                }}
            >
                <div className="accordion-content">{content}</div>
            </div>
        </div>
    );
};


const Accordion = () => {
    const [activeIndex, setActiveIndex] = useState(null);

    const toggleItem = (index) => {
        setActiveIndex(activeIndex === index ? null : index);
    };

    const items = [
        { title: "What is The Levitation?", content: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus accumsan purus gravida sapien consectetur, non pharetra ligula vehicula. Duis non." },
        { title: "How does payment work and how do I get access?", content: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus accumsan purus gravida sapien consectetur, non pharetra ligula vehicula. Duis non." },
        { title: "What devices are supported?", content: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus accumsan purus gravida sapien consectetur, non pharetra ligula vehicula. Duis non." },
        { title: "How long is the content available?", content: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus accumsan purus gravida sapien consectetur, non pharetra ligula vehicula. Duis non." },
        { title: "Is it possible to share content with friends via Telegram?", content: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus accumsan purus gravida sapien consectetur, non pharetra ligula vehicula. Duis non." },
    ];

    return (
        <div className="accordion">
            {items.map((item, index) => (
                <AccordionItem
                    key={index}
                    title={item.title}
                    content={item.content}
                    isOpen={activeIndex === index}
                    onClick={() => toggleItem(index)}
                />
            ))}
        </div>
    );
};

export default Accordion;
