import React, { useState, useRef, useEffect } from 'react';
import { gsap } from 'gsap';
import './AnimatedBlock.css';

const AnimatedBlock = () => {
    const [activeIndex, setActiveIndex] = useState(0);
    const [isSmallScreen, setIsSmallScreen] = useState(window.innerWidth <= 768);
    const imageBlockRef = useRef(null);
    const scrollSectionRef = useRef(null);
    const sections = [
        { text: 'Why choose The Levitation:', subtitle: 'Unique content — exclusive access, only through the Telegram bot.', image: '/levitation_gh/images/product-section/feature.png' },
        { text: '5-hour musical journey', subtitle: 'Unique tracks for all stages of the trip.', image: '/levitation_gh/images/product-section/feature2.png' },
        { text: 'Guidance for a safe experience', subtitle: 'Instructions for a comfortable and safe journey.', image: '/levitation_gh/images/product-section/feature3.png' },
        { text: 'Trip hacks', subtitle: 'Useful tips to make your experience as enjoyable and safe as possible.', image: '/levitation_gh/images/product-section/feature4.png' },
        { text: 'Convenient menu in the Telegram bot', subtitle: 'Easy and quick navigation through all materials for your convenience.', image: '/levitation_gh/images/product-section/feature5.png' },
        { text: 'All information in the bot is available in 5 languages:', subtitle: 'English, French, Russian, Spanish, German.', image: '/levitation_gh/images/product-section/feature6.png' }
    ];

    // Создаем массив рефов для текста
    const textRefs = useRef(sections.map(() => React.createRef()));

    useEffect(() => {
        // Следим за каждым текстовым блоком
        textRefs.current.forEach((ref, index) => {
            const observer = new IntersectionObserver(
                ([entry]) => {
                    if (entry.isIntersecting) {
                        setActiveIndex(index); // Обновляем текущий индекс видимого текста
                    }
                },
                {
                    threshold: 0.3, // 50% текста должно быть видно
                    rootMargin: '0px 0px -30% 0px', // Сдвигаем границу, чтобы срабатывало при достижении центра экрана
                }
            );

            if (ref.current) {
                observer.observe(ref.current);
            }

            return () => observer.disconnect();
        });
    }, []);

    useEffect(() => {
        const handleResize = () => {
            setIsSmallScreen(window.innerWidth <= 768);
        };

        window.addEventListener("resize", handleResize);

        return () => {
            window.removeEventListener("resize", handleResize);
        };
    }, []);

    useEffect(() => {
        // Сначала сбрасываем анимацию всех картинок
        gsap.to('.image', {
            scale: 1, // Уменьшаем все картинки
            opacity: 0, // Прячем их
            duration: 0.4, // Плавная анимация
            ease: 'power2.out', // Естественное ускорение/замедление
        });

        // Анимация текущей (уходящей) картинки: увеличение и исчезновение
        gsap.to(`.image-${activeIndex}`, {
            scale: 1.2, // Увеличиваем перед исчезновением
            opacity: 0, // Прячем картинку
            duration: 0.4, // Длительность анимации
            ease: 'power2.inOut',
        });

        // Анимация следующей (появляющейся) картинки
        gsap.fromTo(
            `.image-${activeIndex}`,
            { scale: 1, opacity: 0 }, // Начальное состояние
            {
                scale: 1.2, // Увеличиваем до нормального размера
                opacity: 1, // Показываем картинку
                duration: 0.3, // Длительность анимации
                ease: 'power2.inOut',
                delay: 0.4, // Пауза, чтобы дождаться завершения анимации предыдущей картинки
            }
        );

        // Анимация текста: сброс цвета и изменение активного текста
        gsap.to('.text', { color: '#f7f5ff00', duration: 0.3 });
        gsap.to(`.text-${activeIndex}`, { color: '#fff', duration: 0.3 });
    }, [activeIndex]);

    useEffect(() => {
        const handleScroll = () => {
            if (!imageBlockRef.current || !scrollSectionRef.current) return;

            const section = scrollSectionRef.current;
            const imageBlock = imageBlockRef.current;
            const sectionTop = section.getBoundingClientRect().top;
            const sectionHeight = section.offsetHeight;
            const windowHeight = window.innerHeight;

            // Фиксируем блок с картинкой
            if (sectionTop <= 0 && sectionTop + sectionHeight > windowHeight) {
                imageBlock.style.position = 'fixed';
                imageBlock.style.top = '0px';
            }
            // Когда прокрутили до конца секции
            else if (sectionTop + sectionHeight <= windowHeight) {
                imageBlock.style.position = 'absolute';
                imageBlock.style.top = `${sectionHeight - windowHeight}px`;
            }
            // Когда секция еще не достигнута
            else {
                imageBlock.style.position = 'absolute';
                imageBlock.style.top = '0';
            }
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <div className="scroll-section" ref={scrollSectionRef}>
        <div className="section-title">
            <div className="title-left">PRODUCT FEATURES</div>
            <div className="title-right">We've gathered all the key points so you can appreciate each advantage of our approach and feel its unique value.</div>
        </div>
            <div className="image-block" ref={imageBlockRef}>
                {isSmallScreen ? (
                    <>
                        <img src="/levitation_gh/images/product-section/group1.png" alt="" className="background-img background-img1" />
                        <img src="/levitation_gh/images/product-section/group2.png" alt="" className="background-img background-img2" />
                    </>
                ) : (
                    <></>
                )}
                <div className="image-container">
                    <div className='image-border'></div>
                    {sections.map((section, index) => (
                        <img
                            key={index}
                            src={section.image}
                            alt={`Image ${index}`}
                            className={`image image-${index}`}
                            style={{ opacity: index === activeIndex ? 1 : 0 }}
                        />
                    ))}
                </div>
            </div>
            <div className="text-block">
                {sections.map((section, index) => (
                    <div
                        key={index}
                        ref={textRefs.current[index]}
                        className={`text text-${index}`}
                    >
                        <div className="text-wrapper">
                            <div className="text-title">
                                {section.text}
                            </div>
                            <div className="text-subtitle">
                                {section.subtitle}
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default AnimatedBlock;
