const CustomPrevArrow = (props) => {
    const { className, onClick } = props;

    return (
        <div
            className={className}
            onClick={onClick}
        />
    );
};

const CustomNextArrow = (props) => {
    const { className, onClick } = props;
    return (
        <div
            className={className}
            onClick={onClick}
        />
    );
};

const ReviewsSection = () => {
    const { ref, inView } = useInView({
        threshold: 0.2, // 20% видимости
        triggerOnce: false, // Если нужно, чтобы анимация повторялась при каждом попадании в зону
    });

    const settings = {
        dots: true,
        infinite: false, // Сделать слайдер не бесконечным
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        fade: true, // Добавлено для эффекта fade
        // autoplay: true, // Добавлено для автоматического листания
        // autoplaySpeed: 6000, // Интервал в 6 секунд
        appendDots: dots => (
            <div className="slick-dots">
                <ul> {dots} </ul>
            </div>
        ),
        customPaging: i => (
            <div className="custom-dot"></div>
        ),
        prevArrow: <CustomPrevArrow />,
        nextArrow: <CustomNextArrow />
    };

    return (
        <section ref={ref} className={`reviews-section ${inView ? 'animate' : ''}`}>
            <img src="/levitation_gh/images/reviews-section/layers-bg-up.png" alt="" className={`layers-bg ${inView ? 'animate' : ''} layers-bg-up`} />
            <img src="/levitation_gh/images/reviews-section/layers-bg-down.png" alt="" className={`layers-bg ${inView ? 'animate' : ''} layers-bg-down`} />
            <div className="container" data-aos="fade">
                <div className="reviews-title-container">
                    <div className="title">Our Supporters</div>
                    <div className="subtitle">Artists who supported us.</div>
                </div>
                <Slider {...settings}>
                    {reviews.map((review, index) => (
                        <div key={index} className="review-slide">
                            <div className="image-container">
                                <img src="/levitation_gh/images/reviews-section/img-border.png" alt="Review-border" className='review-image-border' />
                                <img src={review.image} alt="Review" className="review-image" />
                            </div>
                            <div className="review-content">
                                <h2 className="review-title">{review.title}</h2>
                                <p className="review-description">{review.description}</p>
                                <div className="review-links">
                                    {review.links.map((link, linkIndex) => (
                                        <a
                                            key={linkIndex}
                                            href={review.linksUrl[linkIndex]}
                                            target="_blank"
                                            rel="noreferrer">
                                            <div
                                                className="link-button"
                                                style={{ backgroundImage: `url(${link})` }}
                                            />
                                        </a>
                                    ))}
                                </div>
                            </div>
                        </div>
                    ))}
                </Slider>
            </div>
        </section>
    );
};

export default ReviewsSection;