import React, { useState, useEffect } from "react";
import './Header.css';
import './HeaderMediaRequires.css';
import AnimatedLink from '../AnimatedLink/AnimatedLink';
import { useNavigate } from 'react-router-dom';

const Header = ({ setButtonNavigation }) => {
    const [isSmallScreen, setIsSmallScreen] = useState(window.innerWidth <= 768);
    const [isMenuOpen, setMenuOpen] = useState(false);
    const [isAnimating, setIsAnimating] = useState(false);
    const navigate = useNavigate();

    const handleNavigate = () => {
        const htmlElement = document.documentElement;

        setButtonNavigation(true); // Устанавливаем, что переход был по кнопке
        setIsAnimating(true); // Начать анимацию
        // document.body.classList.add('no-scroll');
        // htmlElement.classList.add('no-scroll');

        setTimeout(() => {
            navigate('/pay');
        }, 1000); // Устанавливаем время, соответствующее длительности анимации

    };


    useEffect(() => {
        const handleResize = () => {
            setIsSmallScreen(window.innerWidth <= 768);
        };

        window.addEventListener("resize", handleResize);

        return () => {
            window.removeEventListener("resize", handleResize);
        };
    }, []);

    useEffect(() => {
        const htmlElement = document.documentElement;

        if (isMenuOpen) {
            document.body.classList.add('no-scroll'); // Добавляем блокировку скролла
            htmlElement.classList.add('no-scroll');
        } else {
            document.body.classList.remove('no-scroll'); // Убираем блокировку
            htmlElement.classList.remove('no-scroll');
        }

        return () => {
            document.body.classList.remove('no-scroll'); // Удаляем класс при размонтировании
            htmlElement.classList.remove('no-scroll');
        };
    }, [isMenuOpen]);

    const toggleMenu = () => {
        setMenuOpen(!isMenuOpen);
        document.body.style.overflow = !isMenuOpen ? 'hidden' : 'auto'; // Отключение/включение скролла
    };

    const closeMenu = () => {
        setMenuOpen(false);
        document.body.style.overflow = 'auto'; // Возвращаем скролл
    };

    const scrollToSection = (sectionId) => {
        const section = document.getElementById(sectionId);
        if (section) {
            section.scrollIntoView({ behavior: 'smooth' });
            closeMenu(); // Закрываем меню после перехода
        }
    };
    return (
        <>
            <header className="header">
                <div className="container">
                    <nav className={`navbar ${isMenuOpen ? 'menu-open' : ''}`}>
                        <a data-aos="zoom-out-down" className="navbar-brand" href="/">
                            {/* LOGO */}
                            <img src="/levitation_gh/logo.png" alt="" />
                        </a>
                        {isSmallScreen ? (
                            <button className="navbar-toggler" onClick={toggleMenu}>
                                {/* ☰ */}
                                <img src="/levitation_gh/images/header/burger-menu-icon.svg" alt="" />
                            </button>
                        ) : (
                            <></>
                        )}
                        <div className={`navbar-collapse ${isMenuOpen ? 'show' : ''}`}>
                            {isSmallScreen ? (
                                <>
                                    <img className="header-layers-bg layers-bg-up" src="/levitation_gh/images/header/layers-bg-up.png" alt="" />
                                    <img className="burger-menu-bg" src="/levitation_gh/images/header/burger-menu-bg.png" alt="" />
                                    <img className="header-layers-bg layers-bg-down" src="/levitation_gh/images/header/layers-bg-down.png" alt="" />

                                    <button className="close-menu" onClick={closeMenu}>
                                        {/* ✖ */}
                                        <img src="/levitation_gh/images/header/burger-menu-icon-close.svg" alt="" />
                                    </button>
                                </>
                            ) : (
                                <></>
                            )}
                            <div className="navbar-nav">
                                <div
                                    onClick={() => scrollToSection('recieve-section')}
                                    className="nav-item"
                                    data-aos="zoom-out-down"
                                    data-aos-delay="100">
                                    <AnimatedLink text="About" initialDelay={0} />
                                </div>
                                <div
                                    onClick={() => scrollToSection('video-section')}
                                    className="nav-item"
                                    data-aos="zoom-out-down"
                                    data-aos-delay="200">
                                    <AnimatedLink text="Video" initialDelay={200} />
                                </div>
                                <div
                                    onClick={() => scrollToSection('product-section')}
                                    className="nav-item"
                                    data-aos="zoom-out-down"
                                    data-aos-delay="300">
                                    <AnimatedLink text="Features" initialDelay={400} />
                                </div>
                                <div
                                    onClick={() => scrollToSection('guide-section')}
                                    className="nav-item"
                                    data-aos="zoom-out-down"
                                    data-aos-delay="400">
                                    <AnimatedLink text="Feedback" initialDelay={600} />
                                </div>
                                <div
                                    onClick={() => scrollToSection('faq-section')}
                                    className="nav-item"
                                    data-aos="zoom-out-down"
                                    data-aos-delay="500">
                                    <AnimatedLink text="FAQ" initialDelay={800} />
                                </div>
                            </div>
                        </div>
                        {isSmallScreen ? (
                            <></>
                        ) : (
                            <a
                                onClick={handleNavigate}
                                className="navbar-button"
                                data-aos="zoom-out-down"
                                data-aos-delay="600"
                            >
                                {/* <button className='btn'>Buy Now</button> */}
                                Buy Now
                            </a>
                        )}
                    </nav>
                </div>
            </header>
            {/* <div className={`animation-transition ${isAnimating ? 'animating' : ''}`} ></div> */}
        </>
    )
}

export default Header;