// App.js
import React, { useEffect, useState } from 'react';
import 'aos/dist/aos.css';
import './App.css';
import AOS from 'aos';
import Header from './components/Header/Header';
import Home from './pages/Home/Home';
import Footer from './components/Footer/Footer';
// import PopUp from './components/PopUp/PopUp';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'; // Импортируем Routes вместо Switch
import PayPage from './pages/PayPage/PayPage';
import TestPage from './pages/TestPage/TestPage';

function App() {
  // const [isPopUpOpen, setIsPopUpOpen] = useState(false);
  const [buttonNavigation, setButtonNavigation] = useState(false); // Состояние для отслеживания перехода по кнопке
  const [isHeaderAnimating, setIsHeaderAnimating] = useState(false); // Состояние для анимации хедера

  // const openPopUp = () => setIsPopUpOpen(true);
  // const closePopUp = () => setIsPopUpOpen(false);

  const triggerHeaderAnimation = () => {
    setIsHeaderAnimating(true);
    setTimeout(() => {
      setIsHeaderAnimating(false); // Убираем анимацию через 1 секунду
    }, 1000);
  };

  useEffect(() => {
    AOS.init({
      duration: 400,
      easing: 'ease-in-out',
      once: true,
    });
  }, []);

  const scrollToSection = (id) => {
    const target = document.getElementById(id);
    if (target) {
      const startPosition = window.pageYOffset;
      const targetPosition = target.getBoundingClientRect().top;
      const duration = 2000; // Длительность прокрутки (в миллисекундах)
      let start = null;

      const easeInOutQuad = (time, from, distance, duration) => {
        time /= duration / 2;
        if (time < 1) return (distance / 2) * time * time + from;
        time--;
        return (-distance / 2) * (time * (time - 2) - 1) + from;
      };

      const animation = (currentTime) => {
        if (!start) start = currentTime;
        const timeElapsed = currentTime - start;
        const scroll = easeInOutQuad(
          timeElapsed,
          startPosition,
          targetPosition,
          duration
        );
        window.scrollTo(0, scroll);

        if (timeElapsed < duration) {
          requestAnimationFrame(animation);
        }
      };

      requestAnimationFrame(animation);
    }
  };

  return (
    <Router basename={process.env.PUBLIC_URL}> {/* Обернули в Router с правильным basename для деплоя на GitHub Pages */}
      <Routes>
        {/* Страница Home с хедером и футером */}
        <Route
          exact
          path="/"
          element={
            <>
              <Header
                scrollToSection={scrollToSection}
                setButtonNavigation={setButtonNavigation}
                isAnimating={isHeaderAnimating}
              />
              <Home 
                setButtonNavigation={setButtonNavigation}
              />
              <Footer scrollToSection={scrollToSection} />
            </>
          }
        />
        {/* Страница About без хедера и футера */}
        <Route
          path="/pay"
          element={
            <PayPage
              buttonNavigation={buttonNavigation}
              triggerHeaderAnimation={triggerHeaderAnimation}
            />
          } />
          <Route 
            path='/test'
            element= {
              <TestPage />
            }
          />
        {/* Можно добавить другие страницы, если необходимо */}
      </Routes>
      {/* <PopUp isOpen={isPopUpOpen} closePopUp={closePopUp} /> */}
    </Router>
  );
}

export default App;
