import React, { useRef, useState } from 'react';
import Slider from 'react-slick';
import './Slider.css'; // Стиль для слайдера

const SliderComponent = () => {
  const [currentSlide, setCurrentSlide] = useState(0); // Для отслеживания текущего слайда
  const sliderRef = useRef(null); // Ссылка на слайдер для управления им

  const slides = [
    {
      text: 'Why choose The Levitation:',
      subtitle: 'Unique content — exclusive access, only through the Telegram bot.',
      image: '/levitation_gh/images/product-section/feature.png'
    },
    {
      text: '5-hour musical journey',
      subtitle: 'Unique tracks for all stages of the trip.',
      image: '/levitation_gh/images/product-section/feature2.png'
    },
    {
      text: 'Guidance for a safe experience',
      subtitle: 'Instructions for a comfortable and safe journey.',
      image: '/levitation_gh/images/product-section/feature3.png'
    },
    {
      text: 'Trip hacks',
      subtitle: 'Useful tips to make your experience as enjoyable and safe as possible.',
      image: '/levitation_gh/images/product-section/feature4.png'
    },
    {
      text: 'Convenient menu in the Telegram bot',
      subtitle: 'Easy and quick navigation through all materials for your convenience.',
      image: '/levitation_gh/images/product-section/feature5.png'
    },
    {
      text: 'All information in the bot is available in 5 languages:',
      subtitle: 'English, French, Russian, Spanish, German.',
      image: '/levitation_gh/images/product-section/feature6.png'
    }
  ];

  const settings = {
    dots: false, // Отключаем стандартные точки навигации
    infinite: true, // Бесконечный слайд
    speed: 500, // Скорость анимации
    slidesToShow: 1, // Показывать один слайд за раз
    slidesToScroll: 1, // Прокручивать по одному слайду
    autoplay: false, // Отключаем автопроигрывание, чтобы контролировать это вручную
    fade: true, // Отключаем стандартное "плавное" переключение
    arrows: false, // Отключаем стандартные стрелки
    beforeChange: (current, next) => {
      setCurrentSlide(next); // Обновляем текущий слайд перед сменой
    },
  };

  // Функции для управления слайдером через кастомные кнопки
  const nextSlide = () => sliderRef.current.slickNext();
  const prevSlide = () => sliderRef.current.slickPrev();

  return (

    <div className="slider-container">
      <div className="section-title">
        <div className="title-left">PRODUCT FEATURES</div>
        <div className="title-right">We've gathered all the key points so you can appreciate each advantage of our approach and feel its unique value.</div>
      </div>
      <Slider ref={sliderRef} {...settings}>
        {slides.map((slide, index) => (
          <div
            key={index}
            className={`slider-item ${currentSlide === index ? 'active' : ''}`}
          >
            {/* Блок с изображением */}
            <div className={`slider-image-wrapper ${currentSlide === index ? 'active' : ''}`}>
              <div className='image-border'></div>
              <img src={slide.image} alt="slide" className="slider-image" />
            </div>

            {/* Кастомные кнопки для управления слайдером */}
            <div className="slider-buttons">
              <button onClick={prevSlide} className="slider-btn btn-prev">
                <img src="/levitation_gh/images/product-section/btn-prev.svg" alt="" />
              </button>
              <button onClick={nextSlide} className="slider-btn btn-next">
                <img src="/levitation_gh/images/product-section/btn-next.svg" alt="" />
              </button>
            </div>

            {/* Блок с текстом */}
            <div className="slider-text-wrapper">
              <div className="text-title">
                {slide.text}
              </div>
              <div className="text-subtitle">
                {slide.subtitle}
              </div>
            </div>
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default SliderComponent;
